import { IRegion } from "../interfaces/IRegion";
import { ICredentials, IUserDetails, TCredentials } from "../interfaces/IUser";
import { get, post } from "./publicHttps";

const entity = "";
const urls = {
  login: "auth/moderator/signin",
};

export const Login = async (data: TCredentials): Promise<any> => {
  const rep = await post(urls.login, data);
  switch (rep.status) {
    case 200:
      localStorage.setItem("user", JSON.stringify(rep.data));
      return true;
    case 401:
      return false;
      break;
    default:
      return false;
  }
};
