import axios, { AxiosError } from "axios";
import { error } from "console";
import { useState } from "react";
import authHeader from "./AuthSvc";

const base_url = "https://reportitws.herokuapp.com/api/";

export const get: any = async (ePoint: string, params?: any): Promise<any> => {
  try {
    let header = authHeader();
    let res: Response;
    if (header.Authorization) {
      res = await axios.get(base_url + ePoint, { headers: header });
      return res;
    }
  } catch (error) {
    return error;
  }
};

export const post = async (
  ePoint: string,
  data: any,
  params?: any
): Promise<any> => {
  try {
    let header = authHeader();
    let res: Response;
    if (header.Authorization) {
      res = await axios.post(base_url + ePoint, data, { headers: header });
      return res;
    }
  } catch (error: any) {
    return error.response;
  }
};

export const put = (ePoint: string, data: any): Promise<any> => {
  return axios.put(base_url + ePoint, data);
};
