import { IRegion } from "../interfaces/IRegion";
import { IReport, IReportType } from "../interfaces/IReport";
import { get as prGet } from "./privateHttps";
import { get as puGet } from "./publicHttps";
const entity = "reportType/";
const urls = {
  all: entity + "all",
};

export const All = async (): Promise<IReportType[]> => {
  const rep = await puGet(urls.all);
  return rep.data;
};
