// import { Marker, Map } from "pigeon-maps";
import { CircleMarker, MapContainer, Popup, TileLayer } from "react-leaflet";
import React, { useEffect, useState } from "react";
import Typography from "@mui/material/Typography";
import {
  IReport,
  IReportType,
  ISearchParam,
  IState,
} from "../../interfaces/IReport";
import "./index.css";
import { ByRegion as ReportByRegion, Search } from "../../services/ReportSvc";
import ResultMap from "./Map";
import Loader from "../loader";
import {
  Box,
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Modal,
  Select,
  Tab,
  TextField,
} from "@mui/material";
import { All as AllReportType } from "./../../services/ReportTypeSvc";
import { useForm } from "react-hook-form";
import { TabContext, TabList, TabPanel } from "@mui/lab";
import ReportModal from "../card/ReportModal";
import ReportList from "./ReportList";

export default function Filter() {
  const [reports, setReports] = useState<IReport[]>([]);
  const [report, setReport] = useState<IReport>();
  const [isLoading, setIsLoading] = useState(true);
  // const [isLoadingAll, setIsLoadingAll] = useState(false);
  const [open, setOpen] = React.useState(false);
  const [reportTypes, setReportTypes] = useState<IReportType[]>();
  const states = [
    { value: -1, label: "Tous" },
    { value: 2, label: "Assignee" },
    { value: 3, label: "En cours" },
    { value: 4, label: "Terminer" },
  ];
  useEffect(() => {
    setIsLoading(true);
    console.log("start");

    getReportTypes();
    console.log("types");

    getReports();
    console.log("reports");

    setIsLoading(false);
    console.log("end");
  }, []);
  const getReportTypes = async () => {
    let data = await AllReportType();
    setReportTypes(data);
  };
  const getReports = async () => {
    let user: any;
    if (localStorage.getItem("user")) {
      user = localStorage.getItem("user");
      user = JSON.parse(user);
      let data = await ReportByRegion(user.region);
      console.log(data);
      setReports(data);
    }
  };
  const handleOpen = (report: IReport): void => {
    setReport(report);
    setOpen(true);
  };
  const handleClose = () => setOpen(false);
  const {
    register,
    setValue,
    handleSubmit,
    formState: { errors },
  } = useForm<ISearchParam>();

  const onSubmit = handleSubmit(async (data) => {
    setIsLoading(true);
    await Search(data).then((data) => {
      setReports(data);
    });
    setIsLoading(false);
  });
  const [tab, setTab] = React.useState("1");

  const handleTab = (event: React.SyntheticEvent, newValue: string) => {
    setTab(newValue);
  };

  const styles = {
    child: {
      margin: "40px",
    },
    select: {
      width: 250,
    },
  };
  return (
    <div>
      <Typography variant="h3" color="#1e242b">
        Vos signalements
      </Typography>
      <form
        onSubmit={onSubmit}
        style={{
          display: "flex",
          flexWrap: "nowrap",
          justifyContent: "center",
          alignItems: "center",
          alignContent: "center",
          width: "100%",
          height: "150px",
          overflow: "auto",
          flexDirection: "row",
        }}
      >
        <FormControl>
          <InputLabel id="demo-simple-select-label">Types</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            label="Types"
            style={styles.select}
            defaultValue=""
            {...register("reportType")}
          >
            {reportTypes?.map((reportType, key) => {
              return (
                <MenuItem key={key} value={reportType.id}>
                  {reportType.name}
                </MenuItem>
              );
            })}
          </Select>
        </FormControl>
        <FormControl>
          <InputLabel id="demo-simple-select-label">Status</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            label="Types"
            style={styles.select}
            defaultValue=""
            {...register("state")}
          >
            {states?.map((state, key) => {
              return (
                <MenuItem key={key} value={state.value}>
                  {state.label}
                </MenuItem>
              );
            })}
          </Select>
        </FormControl>
        <TextField
          id="date"
          label="Birthday"
          type="date"
          defaultValue=""
          style={styles.select}
          sx={{ width: 220 }}
          InputLabelProps={{
            shrink: true,
          }}
          {...register("LDate")}
        />
        <TextField
          id="date"
          label="Birthday"
          type="date"
          defaultValue=""
          sx={{ width: 220 }}
          style={styles.select}
          InputLabelProps={{
            shrink: true,
          }}
          {...register("HDate")}
        />
        <Button type="submit" variant="text">
          Text
        </Button>
      </form>
      <div>
        {isLoading ? (
          <div
            style={{
              display: "flex",
              flexWrap: "nowrap",
              justifyContent: "center",
              alignItems: "center",
              alignContent: "center",
              height: "100vh",
              overflow: "auto",
              flexDirection: "column",
              gap: 10,
            }}
          >
            <Loader></Loader>
          </div>
        ) : (
          <div>
            <TabContext value={tab}>
              <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
                <TabList onChange={handleTab} aria-label="lab API tabs example">
                  <Tab label="Liste" value="1" />
                  <Tab label="Carte" value="2" />
                </TabList>
              </Box>
              <TabPanel value="1">
                <ReportList
                  PReports={reports ? reports : []}
                  PIsLoading={isLoading}
                ></ReportList>
              </TabPanel>
              <TabPanel value="2">
                <ResultMap
                  PReports={reports}
                  PIsLoading={isLoading}
                  PFHandleOpen={handleOpen}
                ></ResultMap>
              </TabPanel>
            </TabContext>
          </div>
        )}
      </div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <div>
          {report ? (
            <>
              <ReportModal report={report}> </ReportModal>
            </>
          ) : (
            <>
              <Typography variant="h5" color="initial">
                Report not found{" "}
              </Typography>
            </>
          )}
        </div>
      </Modal>
    </div>
  );
}
