import { IRegion } from "../interfaces/IRegion";
import { IReport, ISearchParam } from "../interfaces/IReport";
import { get as prGet } from "./privateHttps";
import { get as puGet } from "./publicHttps";
import { GetUserDetails } from "./../hooks/useUserAuth";
import { IUserDetails } from "../interfaces/IUser";
const entity = "report/";
const urls = {
  byRegion: entity + "region/",
  byId: entity,
  search: entity + "search?search=",
};

export const ByRegion = async (region: IRegion): Promise<IReport[]> => {
  const rep = await prGet(urls.byRegion + region.id);
  return rep.data;
};

export const ById = async (reportId: number): Promise<IReport> => {
  const rep = await puGet(urls.byId + reportId);
  return rep.data;
};

export const FindByStateByRegion = async (
  state: number
): Promise<IReport[]> => {
  const user: IUserDetails = GetUserDetails();
  const rep = await prGet(
    urls.search + "state:" + state + " AND region.id : " + user.region.id
  );
  return rep.data;
};

export const Search = async (params: ISearchParam) => {
  const keys = Object.keys(params);
  const values = Object.values(params);
  const user: IUserDetails = GetUserDetails();
  let criterias = "region.id : " + user.region.id;
  for (let index = 0; index < keys.length; index++) {
    switch (keys[index]) {
      case "reportType":
        if (values[index] !== "")
          criterias = criterias + " AND type.id :" + values[index];
        break;
      case "state":
        if (values[index] !== "") {
          if (criterias === "")
            criterias = criterias + " " + keys[index] + ":" + values[index];
          else
            criterias = criterias + " AND " + keys[index] + ":" + values[index];
        }
        break;
      case "LDate":
        if (values[index] !== "") {
          if (criterias === "")
            criterias = criterias + " created_at > " + values[index];
          else criterias = criterias + "AND created_at > " + values[index];
        }
        break;
      case "HDate":
        if (values[index] !== "") {
          if (criterias === "")
            criterias = criterias + " created_at < " + values[index];
          else criterias = criterias + "AND created_at < " + values[index];
        }
        break;
      default:
        break;
    }
  }
  const data = await prGet(urls.search + criterias);

  console.log(urls.search + criterias);

  return data.data;
};
