import {
  Button,
  Container,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  TextField,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import Loader from "../components/loader";
import { TCredentials } from "../interfaces/IUser";
import { IRegion } from "./../interfaces/IRegion";
import { All as allRegions } from "./../services/RegionSvc";

import { useForm, NestedValue } from "react-hook-form";

import { Login as login } from "./../services/ModeratorSvc";
import { Oval } from "react-loader-spinner";
import { useNavigate } from "react-router-dom";

export default function Login() {
  const [regions, setRegions] = useState<IRegion[]>([{ id: 0, name: "" }]);
  const [isLoading, setIsLoading] = useState(true);
  const [isConnecting, setIsConnecting] = useState(false);
  const [badCredentials, setBadCredentials] = useState(false);
  const [credentials, setCredentials] = useState<TCredentials>();
  const history = useNavigate();

  useEffect(() => {
    setIsLoading(true);
    allRegions().then((data) => {
      setRegions(data);
    });
    setIsLoading(false);
  }, []);

  const styles = {
    child: {
      margin: "5px",
    },
    select: {
      width: 250,
    },
  };

  const {
    register,
    setValue,
    handleSubmit,
    formState: { errors },
  } = useForm<TCredentials>();

  const onSubmit = handleSubmit(async (data) => {
    setIsConnecting(true);
    setCredentials({
      username: data.username,
      password: data.password,
      region: findRegion(data.region),
    });
    if (credentials) {
      let access = await login(credentials);
      if (access) {
        history("/moderator/dashboard");
      } else {
        setBadCredentials(true);
      }
    }
    setIsConnecting(false);
  });

  const findRegion = (region: IRegion | string): IRegion => {
    let reg: IRegion | undefined;
    if (typeof region === "string") {
      reg = regions.find((element) => element.name == (region as string));
    } else {
      reg = regions.find((element) => element.name == (region.name as string));
    }
    if (reg) return reg;
    return regions[0];
  };

  if (isLoading) {
    return (
      <div
        style={{
          display: "flex",
          flexWrap: "nowrap",
          justifyContent: "center",
          alignItems: "center",
          alignContent: "center",
          height: "100vh",
          overflow: "auto",
          flexDirection: "column",
          gap: 10,
        }}
      >
        <Loader></Loader>
      </div>
    );
  }
  return (
    <div
      style={{
        display: "flex",
        flexWrap: "nowrap",
        justifyContent: "center",
        alignItems: "center",
        alignContent: "center",
        height: "100vh",
        overflow: "auto",
        flexDirection: "column",
        gap: 10,
      }}
    >
      <form
        onSubmit={onSubmit}
        style={{
          display: "flex",
          flexWrap: "nowrap",
          justifyContent: "center",
          alignItems: "center",
          alignContent: "center",
          height: "100vh",
          overflow: "auto",
          flexDirection: "column",
          gap: 10,
        }}
      >
        <Typography variant="h4" color="initial">
          Connexion
        </Typography>
        <TextField
          style={styles.child}
          id="outlined-name"
          label="Nom d'utilisateur"
          defaultValue={"moderatorname"}
          {...register("username")}
        />
        <TextField
          style={styles.child}
          id="outlined-name"
          label="Mot de passe"
          defaultValue={"rooter"}
          {...register("password")}
        />
        <FormControl>
          <InputLabel id="demo-simple-select-label">Region</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            label="Region"
            style={styles.select}
            defaultValue=""
            {...register("region")}
          >
            {regions?.map((region, key) => {
              return (
                <MenuItem key={key} value={region.name}>
                  {region.name}
                </MenuItem>
              );
            })}
          </Select>
          {badCredentials ? (
            <Typography style={styles.child} variant="body2" color="red">
              Verifier vos identifiants
            </Typography>
          ) : (
            <></>
          )}
        </FormControl>
        {isConnecting ? (
          <Oval color="#00BFFF" height={30} width={30} />
        ) : (
          <Button type="submit" variant="contained">
            Se connecter
          </Button>
        )}
      </form>
    </div>
  );
}
