import { Typography } from "@mui/material";
import React, { useState } from "react";
import { CircleMarker } from "react-leaflet";
import { MapContainer, Popup, TileLayer } from "react-leaflet";
import { IPrpsReports } from "../../interfaces/IProps";
import { IReport } from "../../interfaces/IReport";

const ResultMap: React.FC<IPrpsReports> = ({ PReports, PFHandleOpen }) => {
  return (
    <>
      {PReports.length > 0 ? (
        <div className="leaflet-container">
          <MapContainer
            center={[PReports[0].latitude, PReports[0].longitude]}
            zoom={11}
          >
            <TileLayer
              attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
            {PReports.map((report, key) => {
              return (
                <div key={key}>
                  <CircleMarker
                    center={[report.latitude, report.longitude]}
                    pathOptions={{ color: report.type.color }}
                    interactive
                  >
                    <Popup>
                      A pretty CSS3 popup. <br /> Easily customizable.
                      <a onClick={() => PFHandleOpen(report)}>Details</a>
                    </Popup>
                  </CircleMarker>
                </div>
              );
            })}
          </MapContainer>
        </div>
      ) : (
        <Typography variant="h5" color="initial">
          Aucuns resultats
        </Typography>
      )}
    </>
  );
};

export default ResultMap;
