import React from "react";

import { Route, Routes } from "react-router-dom";
import ReportCartd from "./components/card/ReportCartd";
import Dashboard from "./components/dashboard/Dashboard";
import Assigned from "./components/ReportList/Assigned";
import Admin from "./pages/Admin";
import Login from "./pages/Login";

function App() {
  return (
    <div className="App">
      <Routes>
        <Route path="/" element={<Login />}></Route>
        <Route path="/moderator/" element={<Admin />}>
          <Route path="/moderator/dashboard" element={<Dashboard />}></Route>
          {/*  */}
          <Route
            path="/moderator/card/:reportId"
            element={<ReportCartd />}
          ></Route>
          {/*  */}
          <Route
            path="/moderator/report/assigned"
            element={<Assigned />}
          ></Route>
          <Route path="/moderator/report/inProcess"></Route>
          <Route path="/moderator/report/done"></Route>
        </Route>
      </Routes>
    </div>
  );
}

export default App;
