import React from "react";
import List from "@mui/material/List";
import Drawer from "@mui/material/Drawer";
import Divider from "@mui/material/Divider";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import InboxIcon from "@mui/icons-material/MoveToInbox";
import MailIcon from "@mui/icons-material/Mail";
import { Icon, StepIcon, Typography } from "@mui/material";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import { Link } from "react-router-dom";
import styled from "styled-components";

const drawerWidth = 240;

interface INavigationItem {
  name: string;
  url: string;
  icon: string;
}

const dashBoardNavigation: INavigationItem[] = [
  {
    name: "Dahboard",
    url: "dashboard",
    icon: "",
  },
];

const updateNavigation: INavigationItem[] = [
  {
    name: "Nouveaux",
    url: "report/assigned",
    icon: "",
  },
  {
    name: "En cours",
    url: "inProcess",
    icon: "",
  },
  {
    name: "Termines",
    url: "inProcess",
    icon: "",
  },
];

const navigations = [dashBoardNavigation, updateNavigation];

const StyledLink = styled(Link)`
  text-decoration: none;
  color: rgb(32, 51, 68);
`;

export default function NavBar() {
  return (
    <div>
      <Drawer
        variant="permanent"
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          [`& .MuiDrawer-paper`]: {
            width: drawerWidth,
            boxSizing: "border-box",
          },
        }}
      >
        <Toolbar />
        <Box sx={{ overflow: "auto" }}>
          {navigations.map((navItems, key) => {
            return (
              <div key={key}>
                <List>
                  {navItems.map((item) => (
                    <StyledLink key={item.name} to={item.url}>
                      <ListItem button>
                        <ListItemIcon>
                          <InboxIcon />
                        </ListItemIcon>
                        <ListItemText primary={item.name} />
                      </ListItem>
                    </StyledLink>
                  ))}
                </List>
                <Divider />
              </div>
            );
          })}
        </Box>
      </Drawer>
    </div>
  );
}
