import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { isArrowFunction } from "typescript";
import { IReport } from "../../interfaces/IReport";
import { ById as ReportById } from "../../services/ReportSvc";
import Loader from "../loader";

export default function ReportCartd() {
  const { reportId } = useParams();
  const [report, setReport] = useState<IReport>();
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    setIsLoading(true);
    if (reportId) getReport(Number(reportId));
    console.log(report);

    setIsLoading(false);
  }, []);

  const getReport = async (reportId: number) => {
    ReportById(reportId).then((data) => {
      setReport(data);
    });
  };

  return (
    <div>
      {isLoading ? (
        <Loader />
      ) : (
        report?.images.map((image, key) => {
          return (
            <img
              key={key}
              src={image.url}
              alt="not found"
              height={50}
              width={50}
            ></img>
          );
        })
      )}
    </div>
  );
}
